import Vue from 'vue'
import Router from 'vue-router'
import grid from '@/components/grid'
import hello from '@/components/HelloWorld'
import resume from '@/components/resume'
import gamePage from "@/components/gamePage";

Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/',
            name: 'Grid',
            component: grid
        },
        {
            path: '/hello',
            name:'Hello World',
            component: hello
        },
        {
            path: '/resume',
            name:'Resume',
            component: resume
        },
        {
            path: '/workplacehazards',
            name:'Workplace Hazards',
            component: gamePage,
            props: {
                urlPath:"https://www.annamariakoshy.com/#/workplacehazards",
                twitchLink:"https://loch-nesrac.itch.io/workplace-hazards",
                theme: "Help",
                name: "Workplace Hazards",
                content: [{
                    float:"left",
                    video:'/assets/videos/wph.mp4',
                    height:"190px",
                    text:[
                        {
                            head:"Welcome to your mandatory Workplace Safety Training Module!",
                            text:"This short interactive guide is here to educate you and a fellow employee about the many dangers and hazards that can occur in the workplace.",
                        },
                        {
                            head:"A local co-op puzzle platformer...",
                            text:"where players must utilize their dead bodies to solve puzzles and reach the exit. Stylized like a workplace safety training module, the levels often put the 'workers' in direct danger and require certain sacrifices to be made.",
                        }
                    ]
                },
                    {float:"left",
                        image:'https://forum.vuejs.org/uploads/default/original/2X/5/555257b8c5e7ecf34ce4f9b952eeaf006adfa339.png',
                    }]
            }
        }
    ],
    data () {
        return {
            publicPath: process.env.BASE_URL
        }
    }
})